import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetFiscal/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class AssetFiscal extends Component {
  state = {
    filter: {
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAssetFiscals } = this.props;
    getAssetFiscals();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetFiscal.error) {
      notification("error", this.props.assetFiscal.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetFiscal.success) {
      notification("success", this.props.assetFiscal.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getAssetFiscals } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    getAssetFiscals(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAssetFiscals } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetFiscals();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getAssetFiscals } = this.props;
    //const { current_page } = this.props.assetFiscal;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account) {
      string += "id_account=" + filter.id_account;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getAssetFiscals(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAssetFiscals } = this.props;
    this.setState({
      filter: {
        id_account: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetFiscals();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateAssetFiscal(id);
    } else {
      this.props.deactivateAssetFiscal(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getAssetFiscals } = this.props;
    const { search } = this.props.assetFiscal;
    const { filter } = this.props.assetFiscal;
    getAssetFiscals(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_asset_fiscal",
    rowKey: "id_asset_fiscal",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="assetFiscals.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="assetFiscals.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "asset_fiscal_name",
        rowKey: "asset_fiscal_name",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "assetFiscals.add.depreciationMethod" }),
        dataIndex: "depreciation_method",
        rowKey: "depreciation_method",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "warning";
            display = getLanguage({ id: "Non Depreciable" });
          } else if (text === 2) {
            className = "active";
            display = getLanguage({ id: "Straight Line" });
          } else {
            className = "primary";
            display = getLanguage({ id: "Double Declining Method" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "assetFiscals.add.estimatedLifeYear" }),
        dataIndex: "estimated_life_year",
        rowKey: "estimated_life_year",
        width: "5%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "assetFiscals.add.depreciationRate" }),
        dataIndex: "depreciation_rate",
        rowKey: "depreciation_rate",
        width: "5%",
        align: "center",
        render: text => <span>{parseFloat(text)}</span>
      },

      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { asset_fiscals } = this.props.assetFiscal;
    //console.log(this.props.assetFiscal); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Asset Fiscal") && columns.length === 5) {
      columns.push(this.listAction);
    }

    Object.keys(asset_fiscals).map((assetFiscal, index) => {
      return dataSource.push({
        ...asset_fiscals[assetFiscal],
        key: assetFiscal
      });
    });

    // Pagination Setup
    const { current_page, page_size, total, loading } = this.props.assetFiscal;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetFiscals.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Asset Fiscal") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="assetFiscals.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "name" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {asset_fiscals.length === 0 && !this.props.assetFiscal.loading ? (
              <Empty
                description={<IntlMessages id="assetFiscals.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.assetFiscal.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    assetFiscal: state.assetFiscal,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AssetFiscal);
